<template>
  <input
      :value="modelValue"
      @input="updateInput"
      class="input"
      type="text"
  >

</template>

<script>
export default {
  name: "MyInput",
  props: {
    modelValue: [String, Number]
  },
  methods: {
    updateInput(event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
}
</script>

<style scoped>
.input {
  width: 100%;
  border: 1px solid teal;
  padding: .5rem;
  margin-top: .5rem;
}

</style>